  <div class="Home">
    <div class="header">
      <div class="logo">
        Mr.Shen
      </div>
      <nav class="nav">
          <span @click="goWeibo">weibo</span>
          <span @click="goGithub">github</span>
          <span @click="goBlog">blog</span>
      </nav>
      <div class="banner">
