        <p>GOOD THINGS</p>
  			<p>
          <span class="btn" v-touch-ripple @click="lookMore">Look More</span>
          <span class="btn" v-touch-ripple @click="lookDemo">Watch Demo</span>
        </p>
  		</div>
    </div>


    <div class="con">
      <div class="row1 row">
        <img src="../assets/woniu.jpg" alt="">
        <div class="row_intro">
          <p class="title">蜗牛之吻 <span>——印度尼西亚三发县</span></p>
