          <span class="btn" v-touch-ripple  @click="lookMore">Look More</span>
        </div>
      </div>
      <div class="row2 row">
        <div class="row_intro">
          <p class="title">爆发中的卡尔布科火山 <span>——智利</span></p>
          <p class="intro">卡尔布科火山在沉睡了40年以后，在2015年喷发了数次。如同科幻片中世界末日一般的火山喷发，伴随着冲上天际的炽热熔岩，漫天的烟雾把火山都覆盖住了。而在浓浓黑雾之间，更是有数道闪电划破夜空，场面十分壮观，也让人对大自然的力量感到恐惧</p>
          <span class="btn" v-touch-ripple @click="lookMore">Look More</span>
        </div>
        <img src="../assets/volcano.jpg" alt="">
      </div>
    </div>
  </div>
</template>

<script>

export default {
  name: 'Home',
  data () {
    return {

    }
  },

  methods: {
    goWeibo () {
      window.location.href="http://weibo.com/3122657741/profile?rightmod=1&wvr=6&mod=personinfo"
    },
    goGithub () {
      window.location.href="https://github.com/shenlll"
    },
    goBlog () {
      window.location.href="https://shenlll.github.io"
    },
    lookMore () {
      this.$router.push('Picture')
    },
    lookDemo () {
      this.$router.push('Demogage')
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="less">
.header {
  background: url("../assets/bg.jpg") no-repeat center 30%;
  height:600px;
  position: relative;
  .logo {
    position: absolute;
    left: 5%;
    top: 80px;
    font-size: 32px;
    transition: 0.3s;
    cursor: pointer;
    &:hover {
