    top: 87px;
  }
}
.nav {
  span {
    color: rgba(0,0,0,0.7);
    position: relative;
    padding: 0 30px;
    font-size: 20px;
    cursor: pointer;
    &:after,&:before{
      position: absolute;
      color: rgb(76, 189, 66);
      opacity: 0;
      transition: 0.3s
    }
    &:before {
      content: '[';
      left: 20%;
    }
    &:after {
      content: ']';
      right: 20%;

    }
    &:hover {
      color: rgba(0,0,0,1);
      &:after{
        opacity: 1;
        right: 0;
      }
      &:before{
        opacity: 1;
        left: 0;
      }
