    text-indent: 2em;
  }

  span:nth-child(2){
    background-color: rgba(0, 0, 0, 0.4)
  }
}
.con {
  width: 70%;
  min-width: 1400px;
  margin: 30px auto 0;
}
.row {
  display: flex;
  margin-bottom: 100px;
  img{
    display: block;
    border-radius: 20px;
    box-shadow: 13px 13px 13px -3px grey;
  }
  .row_intro {
    padding: 50px 150px 50px 120px;
    .title {
      font-size: 24px;
      font-weight: 700;
      transition: 0.3s;
      text-align: left;
      margin-bottom: 50px;
      color: #000;
      text-indent: 0.5em;
      span{
        font-weight: normal;
      }
      cursor: pointer;
      &:hover {
        color: #ef6249;
      }
